import React from 'react';
import {style} from './AutoForm'


let Modal
if (style === 'semantic') {


    Modal = ({children, onCopy, onDownload,onClose}) => {
        return (
            <div>
                <div className="ui active  modal large " style={{top: '70px'}}>
                    <i className="close icon" onClick={onClose}/>
                    <div className="header">Fill</div>
                    <div className="scrolling content" style={{height: 'calc(100vh - 280px)'}}>
                        {children}
                    </div>
                    <div className="actions">
                        <div className="ui button" onClick={onCopy}>Copy</div>
                        <div className="ui button" onClick={onDownload}>Download</div>
                    </div>
                </div>
            </div>)
    }
}


if (style === 'bootstrap3') {
    Modal = ({children, onCopy, onDownload,onClose}) => <div className="modal fade" tabIndex="-1" role="dialog">
        <div className="modal-dialog" role="document">
            <div className="modal-content">
                <div className="modal-header">
                    <button onClick={onClose} type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 className="modal-title">Fill</h4>
                </div>
                <div className="modal-body">
                    <p>One fine body&hellip;</p>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" onClick={onCopy}>Copy</button>
                    <button type="button" className="btn btn-default" onClick={onDownload}>Download</button>
                </div>
            </div>
        </div>
    </div>
}
if (style === 'bootstrap4') {
    Modal = ({children, onCopy, onDownload,onClose}) => <div className="modal" tabIndex="-1" role="dialog">
        <div className="modal-dialog" role="document">
            <div className="modal-content">
                <div className="modal-header">
                    <h5 className="modal-title">Modal title</h5>
                    <button onClick={onClose} type="button" className="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="modal-body">
                    {children}
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" onClick={onCopy}>Copy</button>
                    <button type="button" className="btn btn-default" onClick={onDownload}>Download</button>
                </div>
            </div>
        </div>
    </div>
}


if (style === 'material') {
    Modal = ({children, onCopy, onDownload,onClose}) => {
        const actions = [
            <FlatButton
                label="Cancel"
                primary={true}
                onClick={onCopy}
            />,
            <FlatButton
                label="Submit"
                primary={true}
                keyboardFocused={true}
                onClick={this.handleClose}
            />,
        ];

        return <Dialog
            title="Dialog With Actions"
            actions={actions}
            modal
            open
            onRequestClose={onDownload}
        >
            {children}
        </Dialog>
    }
}


export default Modal